body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.header-box {
  margin: 0;
  width: 100%;
  height: 100px;
  padding-top: 1em;
  background-color:  #e7f9d7;
  text-align: center;
  color: #ffff;
}

span {
  color: #e8e8e8;
}

.fb-image {
  margin-top: -2.1em;
}
.main {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
}

.App {
  margin: 3em 0;
  align-items: center;
  width: auto;
  text-align: center;
  border: 3px solid #e8e8e8;
  border-radius: 5px;
  padding: 1em 0;

}

.bg-image{
  width:100%;
}

.box {
  /* background-color: #e8e8e8; */
  background-color: #f7f9cc;
  padding: 1em;
}


.questions {
  font-size: 1.5em;
}

button {
  margin: 1em 0;
  padding: .5em 1.5em;
  /* background-color: #6b8274; */
  color: #6b8274;
  border-radius: 3px;
  border: 2px solid #6b8274;
  font-size: .8em;
}

input {
  width: 300px;
  padding: .7em 1em;
  border-radius: 3px;
  border: 1px solid #e8e8e8;
}

